import React from "react"
import { Link } from "gatsby"

import styled from "styled-components"

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { window } from "browser-monads"

import Box from "../styles/Box"

import theme from "../styles/theme"

const Nav = styled.nav`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  background: #fafafa;
  height: 80px;
  padding: 8px 16px;
  width: 100%;
`

const LinkStyled = styled(Link)`
  color: rgba(0, 0, 0, 0.65);
  text-decoration: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 0.9rem;
`
const active = {
  color: theme.colors.primary,
}

const Navigation = () => {
  const path = window.location.pathname
  return (
    <Nav>
      <LinkStyled to="/info" activeStyle={active}>
        <FontAwesomeIcon
          icon={[path === "/info" ? "fa" : "fal", "info-circle"]}
          size="lg"
        />
        <Box>Info</Box>
      </LinkStyled>

      <LinkStyled to="/lawn" activeStyle={active}>
        <FontAwesomeIcon
          icon={[path === "/lawn" ? "fa" : "fal", "home"]}
          size="lg"
        />
        <Box>Lawn</Box>
      </LinkStyled>

      <LinkStyled to="/forecast" activeStyle={active}>
        <FontAwesomeIcon
          icon={[path === "/forecast" ? "fa" : "fal", "cloud"]}
          size="lg"
        />
        <Box>Forecast</Box>
      </LinkStyled>

      <LinkStyled to="/lawns/" activeStyle={active}>
        <FontAwesomeIcon
          icon={[path === "/lawns" ? "fa" : "fal", "th-list"]}
          size="lg"
        />
        <Box>Lawns</Box>
      </LinkStyled>
    </Nav>
  )
}

export default Navigation
